@import url(https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;1,300;1,800&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@900&display=swap);
body {
  margin: 0;
  font-family: "Poppins", sans-serif !important;
  margin-bottom: 40px;
  padding-right: 0px !important;
  padding: 0px !important;
  background-color: #f6f7fa !important;
  max-width: 100% !important;
}

@media screen and (max-width: 520px) {
  .asd{
    display:block;
  }
}
.div {
  max-width: 100% !important;
}

.color-me{
  color: white !important;
}
.navbar {
  background-color: #31e197;
  font-weight: bold;
}

p.raices {
  font-size: 5rem;
}
h1.title-nosotros {
  margin-top: 25px;
  margin-bottom: 20px;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
}
h1.donar-page.text-center {
  margin-top: 5px;
  margin-bottom: 20px;
  font-family: "Poppins", sans-serif;
  font-weight: bold;
}
.cards {
  width: 25rem;
  box-shadow: 0px 2px 10px grey;
  border-radius: 10px !important;
  margin: 10px;
  justify-content: center;
  align-content: center;
}

.card-top {
  border-top-left-radius: 10px !important;
  border-top-right-radius: 10px !important;
}

.boton-arriba {
  border-radius: 20rem;
  position: fixed;
  bottom: 0;
  right: 0;
  padding: 0;
  margin-right: 30px;
  margin-bottom: 50px;
  width: 50px;
  height: 50px;
  background-color: #0090ff;
  color: #fff;
  border-color: #5fea88;
  border-width: 0ch;
  transition: all 0.3s ease-out;
  z-index: 100;
}
.boton-arriba:hover {
  width: 70px;
  background-color: #0090ff;
  border-color: #5fea88;
}
.btn-tarjeta {
  width: 50%;
  font-size: larger;
  padding: 15px 30px;
  color: #fff;
  background-color: #0090ff;
  border-color: #17b85a;
  border-radius: 50px;
  border: none;
  transition: all 0.3s ease-out;
}

.btn-tarjeta:hover {
  width: 70%;
  color: #fff;
  background-color: #5fea88;
  border-color: #5fea88;
}
.section1 {
  align-items: center;
}

.section1 h1,
h2,
p {
  text-align: center;
}

.col-sec1 {
  margin-top: 150px;
}

.section3 {
  background-color: #f6f7fa;
}

/*.section4 {
  background-color: #f6f7fa;
}*/
.title-redes {
  margin-top: 0px;
  color: black;
  font-size: 14px;
}
.redes {
  width: 50px;
  height: 50px;
  margin: 20px;
  transition: 0.4s;
  margin-bottom: 5px;
}

.redes:hover {
  transform: rotate(360deg);
}
p.footer.text-mutted.text-center.mb-0 {
  margin-bottom: 45px;
  color: black;
}
.thanx {
  width: 30em;
  max-width: 90%;
}

iframe {
  width: 400px;
  height: 450px;
}
.footer-title {
  font-weight: bold;
  color: black;
  margin-bottom: 0%;
}
.footer {
  background-color: #31e197;
  font-size: x-large;
  color: #fff;
  left: 0;
  padding: 1%;
  margin-top: 1%;
  bottom: 0px;
  width: 100%;
  position: relative;
}

.text-mutted {
  font-size: small;
}

.anim-container {
  margin-bottom: 10px;
}

.nosotros {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  align-self: center;
}
img.img-vol-coor.w-100 {
  box-shadow: 0px 2px 10px grey;
  border-radius: 10px !important;
}
button.btn-volunteer {
  text-align: center;
  width: 270px;
  height: 65px;
  color: #fff;
  background-color: #0090ff;
  border-color: #17b85a;
  border-radius: 50px;
  border-width: 0ch;
  transition: all 0.3s ease-out;
}

button.btn-volunteer:hover {
  text-align: center;
  width: 300px;
  color: #fff;
  background-color: #5fea88;
  border-color: #5fea88;
}
@media screen and (max-width: 411px) {
  p.description {
    font-size: 16px;
  }
  .img-vol-coor {
    height: 243px;
  }

  .btn-btn {
    width: 80%;
    padding: 15px 30px;
    margin-top: 10px;
  }

  .btn-tarjeta {
    width: 70%;
  }

  .flex {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  iframe {
    width: 100%;
    height: 250px;
    margin: 0 auto;
    padding: 0;
  }
}

@media screen and (min-width: 412px) and (max-width: 768px) {
  .btn-btn {
    width: 50%;
    padding: 10px;
    font-size: x-large;
    text-align: center;
    justify-content: center;
    align-items: center;
    align-self: center;
  }

  .boton-arriba {
    width: 70px;
    height: 70px;
  }

  .btn-tarjeta {
    font-size: larger;
  }
}

@media screen and (min-width: 769px) {
  .btn-btn {
    width: 300px;
    padding: 20px;
  }

  .btn-link {
    font-size: x-large !important;
  }
  img.img-vol-coor.w-100 {
    width: 850px !important;
    height: 450px;
  }
  .anim-container {
    height: 400px;
    margin-right: 120px;
    margin-bottom: 0px;
  }
}


